import { IBreadcrumb } from "@/base-ui/breadcrumb";
import { RouteRecordRaw } from "vue-router";

let firstMenu: any = null;

export function mapMenusToRoutes(userMenus: any[]): RouteRecordRaw[] {
  const routes: RouteRecordRaw[] = [];

  // 1. 读取本地所有的路由
  const localRoutes: RouteRecordRaw[] = [];
  const routeFiles = require.context("../router/main", true, /\.ts/);
  routeFiles.keys().forEach((key) => {
    const route = routeFiles(key).default; // 拿到每个路由文件的默认导出对象，用于后续动态注册到路由表中
    localRoutes.push(route);
  });

  // 2. 根据菜单获取需要添加的routes
  // 递归函数实现
  const _recurseGetRoute = (menus: any[]) => {
    for (const menu of menus) {
      if (menu.type === "2") {
        // 如果是需要展示的菜单类型
        const route = localRoutes.find((route) => route.path === menu.path);
        if (route) routes.push(route);
        if (!firstMenu) {
          firstMenu = menu;
        }
      } else {
        // 否则 目录类型，继续进行递归
        _recurseGetRoute(menu.children);
      }
    }
  };

  _recurseGetRoute(userMenus);

  return routes;
}

export function pathMapBreadcrumbs(userMenus: any[], currentPath: string): any {
  const breadcrumbs: IBreadcrumb[] = [];
  pathMapToMenu(userMenus, currentPath, breadcrumbs);
  return breadcrumbs;
}

export function pathMapToMenu(
  userMenus: any[],
  currentPath: string,
  breadcrumbs?: IBreadcrumb[]
): any {
  for (const menu of userMenus) {
    if (menu.type === "1") {
      const findMenu = pathMapToMenu(menu.children || [], currentPath);
      if (findMenu) {
        breadcrumbs?.push({ name: menu.name });
        breadcrumbs?.push({ name: findMenu.name, path: findMenu.path });
        return findMenu;
      }
    } else if (menu.type === "2" && menu.path === currentPath) {
      breadcrumbs?.push({ name: menu.name, path: menu.path });
      return menu;
    }
  }
}

export { firstMenu };
